.site-nav {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	list-style: none;
	margin: 0;
	padding: 0 58px 162px 59px; 
}

.site-nav__item {
	margin-top: 23px;
}

.site-nav__item:first-child {
	margin-right: auto;
}

.site-nav__item:not(:first-child) {
	margin-left: 35px;
}

.site-nav__item:last-child {
margin-left: 107px;
}

.site-nav__link{
	&:hover {
		opacity: 60%;
	}

	&:active {
		opacity: 30%;
	}

}



.site-nav__link {
	font-family: "Gilroy", "Open Sans", "Lucida Sans", sans-serif;
	font-weight: 800;
	font-size: 1.2em;
	line-height: 1.47em;
	letter-spacing: 0.2em;
	text-transform: uppercase;
}	

.site-nav__item:first-child .site-nav__link {
	letter-spacing: 0.1em;
}

